<template>
    <div>
      <el-dialog
        title=""
        :visible.sync="dialogVisible"
        width="30%"
        :before-close="handleClose">
        <el-input type="textarea" :rows="2"
          placeholder=""
          v-model="projectSummary">
        </el-input>
        <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false"> </el-button>
    <el-button type="primary" @click="sendProjectSummary"> </el-button>
  </span>
      </el-dialog>
    </div>
</template>

<script>
    export default {
      name: "projectSummary",
      props:{
        dialogVisible:false
      },
      watch:{
        dialogVisible(){
          this.$emit('update:isShow',this.dialogVisible);
          this.projectSummary = ''
        }
      },
      data() {
        return {
          projectSummary:''
        };
      },
      methods: {
        handleClose() {
          this.dialogVisible = false
        },

        //
        sendProjectSummary(){
          this.$confirm(',,', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            if(this.projectSummary){
              this.$emit('sendProjectSummary', this.projectSummary)
              this.dialogVisible = false
            }else{
              this.$message.error('');
            }

          });
        }
      }
    }
</script>

<style scoped>

</style>



