<template>
  <div>
    <el-dialog :visible.sync="isShow">
      <div class="addTask">
        <el-form  label-width="80px" :model="projectInfo">
          <el-form-item label="" >
            <el-input v-model="projectInfo.name"  placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" >
            <el-select v-model="projectInfo.class" placeholder="" class="select1" size="small">
              <el-option
                v-for="item in classList"
                :key="item._id"
                :label="item.name"
                :value="item._id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="projectInfo.label" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-date-picker
              v-model="projectInfo.timeArr"
              type="daterange"
              range-separator=""
              start-placeholder=""
              end-placeholder="">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="">
            <el-input type="textarea" v-model="projectInfo.description" placeholder=""></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="sendProjectInfo">{{makeSureText}}</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  export default {

    props: {
      isShow: Boolean,
      projectInfo: {
        type: Object,
        default: function () {
          return {}
        }
      },
      makeSureText:{
        type: String,
        default: ' '
      },
      classes:{
        type: Array,
        default: function(){
          return []
        }
      }
    },
    data() {
      return {
        classList: []
      }
    },
    methods:{
      show(){
        this.isShow =  true
      },
      sendProjectInfo(){
        if(!this.projectInfo.name){
          this.$message.error('')
        }else if(this.projectInfo.name.length > 20){
          this.$message.error('20')
        } else if(!this.projectInfo.class){
          this.$message.error('')
        } else{
          this.$emit('sendProjectInfo', this.projectInfo)
        }
      }
    },
    watch:{
      classes:{
        deep:true,
        handler(){
          this.classList = this.classes
        }
      },
      isShow(){
        this.$emit('update:isShow',this.isShow);
      }
    }

  }
</script>



