<template>
  <el-dialog title="" :visible.sync="isShow">
    <div>
      <el-table
        :data="userList">
        <el-table-column prop="name" label="">
          <template slot-scope="scope">
            <el-input size="small" placeholder="" v-model="scope.row.name"></el-input>
          </template>
        </el-table-column>
        <el-table-column prop="studentID" label="/">
          <template slot-scope="scope">
            <el-input size="small" placeholder="/" v-model="scope.row.studentID"></el-input>
          </template>
        </el-table-column>
        <el-table-column prop="role" label="">
          <template slot-scope="scope">
            <el-select v-model="scope.row.role" placeholder="">
              <el-option
                v-for="item in roleList"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </template>
        </el-table-column>
      </el-table>
      <div>
        <el-button type="text" @click="addUser">
          
        </el-button>
      </div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="isShow = false"> </el-button>
      <el-button type="primary" @click="saveUserList"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
  import {Rule} from '../../util/rule'

  export default {
    props: {
      isShow: {
        type: Boolean,
        default: false
      },
      userList:{
        type:Array,
        default:[]
      },
      data:{
        type:Array,
        default:[]
      }
    },
    mounted(){
      window._changeWorkStatus = (id) => {
        this.changeWorkStatus(id)
      }
    },
    data() {
      return {
        roleList: [{
          value: 'P1',
          label: 'P1'
        }, {
          value: 'P2',
          label: 'P2'
        }, {
          value: 'P3',
          label: 'P3'
        }],
        notifyList:{},
      }
    },
    beforeDestroy(){
      window._changeWorkStatus =  null;
    },
    methods: {
      addUser() {
        this.userList.push({})
      },
      changeWorkStatus(id){
         let info = {
          isWorking:true
        };
        this.$http.put("/users/" + id + "/workingStatue",info).then(result=>{
          this.notifyList[id].close();
          if(result.data.code !==200){
            return //todo
          }
          this.$notify({
            message: '',
            type: 'success'
          });
          this.$emit('changeWorkStatus')
        })
      },
      saveUserList() {
        for (let i = 0; i < this.userList.length; i++) {
          let user = this.userList[i];
          if (!user || !user.name || !user.studentID || !user.role) {
            return this.$message.error('')
          }
          if (!Rule.user.name(user.name)) {
            return this.$message.error('' + user.name + '')
          }
          if (!Rule.user.role(user.role)) {
            return this.$message.error('' + user.name + '')
          }
          if (!Rule.user.studentID(user.studentID)) {
            return this.$message.error('' + user.name + '/,6~30')
          }
          let existUserStudentID = this.data.find(userInfo=> {return user.studentID == userInfo.studentID});
          if (existUserStudentID) {
            return this.$message.error(''+existUserStudentID.name+'/');
          }
          let existUserName = this.data.find(userInfo=> {return user.name == userInfo.name});
          if (existUserName) {
            return this.$confirm(existUserName.name + '', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }).then(() => {
              this.createUserList();
            })
          }
        }
        this.createUserList();


      },
      createUserList(){
        this.$http.post('/users', {userList: this.userList}).then(result => {
          console.log(result)
          result.data.forEach(user=>{
            setTimeout(()=>{
              if(user.code === 200110){
                let notify = this.$notify({
                  type: 'error',
                  dangerouslyUseHTMLString: true,
                  duration: 0,
                  message: user.err + '<br><br><button  onclick="_changeWorkStatus(\''+ user.user._id+'\')" class="el-button el-button--primary el-button--mini "></button>'
                });
                this.notifyList[user.user._id] =  notify;
                return
              }

              if(user.code !==200){
                this.$notify({
                  message: user.err,
                  type: 'error'
                });
              }else{
                this.$notify({
                  message: user.user.name +'',
                  type: 'success'
                });
                this.isShow = false;
              }
            },10)
          })
        })
      },
    },
    watch: {
      isShow() {
        this.$emit('update:isShow', this.isShow)
      },
      userList: {
        deep: true,
        handler() {
        }
      }
    }
  }

</script>

<style scoped>

</style>



